.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: var(--space60-90px) 0;
}

.title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: var(--space16-18px);
}

.title p {
  font-size: var(--font10-14px);
}

.link {
  display: flex;
  flex-direction: column;
}

.link li {
  margin: 0 var(--space8-12px) var(--space8-12px);
}

.link a {
  display: block;
  font-weight: bold;
  color: var(--white);
  padding: var(--space8-12px) var(--space30-45px);
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  transition: all 0.3s;
}

.button1 {
  background-color: var(--blue);
}

.button2 {
  background-color: var(--red);
}

.button3 {
  background-color: var(--base-color);
}

.tell {
  font-size: var(--font20-24px);
  font-weight: bold;
  margin-top: var(--space8-12px);
}

.call {
  margin-top: 0.25rem;
}

@media (min-width: 768px) {
  .contact {
    flex-direction: row;
    justify-content: space-around;
  }

  .title {
    width: 30vw;
    margin-bottom: 0;
  }

  .contactBox {
    width: 60vw;
    border-left: 2px solid var(--gray-1);
    padding: var(--space20-30px) clamp(1.25rem, -3.036rem + 8.93vw, 5rem);
  }

  .link {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .link li {
    margin: 0 clamp(0.5rem, -0.071rem + 1.19vw, 1rem) 0;
  }

  .link li a {
    font-size: var(--font16-18px);
    padding: clamp(0.5rem, 0.214rem + 0.6vw, 0.75rem)
      clamp(1rem, -0.143rem + 2.38vw, 2rem);
  }

  .link a:hover {
    box-shadow: none;
    transform: scale(1, 1) translateY(2px);
  }

  .tell {
    border-top: 2px solid var(--gray-1);
    padding-top: var(--space20-30px);
    margin-top: var(--space30-45px);
  }
}
